let state = {
  counter: {
    count: 0
  }
}

let actions = {
  counter: {
    up(state) {
      state.count++
    }
  }
}

let effects = {
  counter: {
    async upAsync(actions) {
      await new Promise(t => setTimeout(t, 1000))
      actions.up()
    }
  }
}

let store = new smox.Store({ state, actions, effects })
store.subscribe(csl)
store.actions.counter.up()
store.actions.counter.up()
store.actions.counter.up()
function csl() {
  console.log(store.state.counter)
}